import { useDispatch, useSelector } from "react-redux";
import { DialogRespondeData, sendMessageFriendTh, getMessageForUserTh, getDialogTh, ItemsMessageType, MessageServerType } from "../../redux/dlgReduser";
import { AppStateType } from "../../redux/reduxStore";
import photoImg from "../../img/img.webp"
import { useEffect } from "react";
import s from './Dialz.module.css';
import { Field, Form, Formik } from "formik";
import { useHistory } from "react-router";
import Qs from "qs";
import { PhotosType } from "../../commonType/commonType";
import { Button } from "antd";
import { Input } from "formik-antd";



export const DialogF: React.FC = (props) => {
	const dispatch = useDispatch()
	const dialogData = useSelector((state: AppStateType) => state.dialogs.dataDialogs)
	const dataMessage = useSelector((state: AppStateType) => state.dialogs.dataMessage)
	const history = useHistory()

	const parsed = Qs.parse(history.location.search.substr(1))
	const getMessageWithFriends = (userId: number) => {
		changeHistory(userId)
		dispatch(getMessageForUserTh(userId))
	}
	const changeHistory = (userId: number) => {
		let searchObj: QsForHistory = {
			id: userId
		}
		history.push({
			pathname: '/dialogs',
			search: Qs.stringify(searchObj)
		})

	}

	useEffect(() => {
		dispatch(getDialogTh())
	}, [dataMessage])

	const dialogBlock = dialogData
		.map((item: DialogRespondeData) => <DialogItemo photo={item.photos} id={item.id} key={item.id}
			userName={item.userName} getMessageWithFriends={getMessageWithFriends} />)

	const sendMess = (body: ForInitialForm) => {
		if (!!parsed.id)
			dispatch(sendMessageFriendTh(+parsed.id, body))
	}

	return (
		<div className={s.wrap}>
			<div className={s.blockDialogs}>{dialogBlock}</div>
			<div className={s.blockMessage}>
				<MessageDialog dataMessage={dataMessage} />
				<div className={s.formSend}>
					<Formik
						initialValues={{
							message: '',
						}}
						onSubmit={(value, { resetForm }) => {
							sendMess(value)
							resetForm()
						}}
					>
						{(props) => (
							<Form>
								{/* <Field name={'message'} maxLength={250} style={{ maxWidth: '60%' }} placeholder={'write message'} /> */}

								<Input.TextArea autoSize={{ minRows: 1, maxRows: 4 }} name={'message'} maxLength={250} style={{ maxWidth: '60%' }} placeholder={'write message'} />
								<span>
									<Button shape="round" type={'primary'} htmlType='submit' >Send</Button>
								</span>
							</Form>
						)}
					</Formik>
				</div>
			</div>
		</div>
	)
}
const DialogItemo: React.FC<PropsTypeDialogItemo> = (props) => {
	return <div className={s.block} onClick={() => { props.getMessageWithFriends(props.id) }}>
		<img src={props.photo.small || photoImg} style={{ width: 50 }} alt="" />
		{props.userName}
	</div>
}
const MessageDialog: React.FC<MessagePropsType> = (props) => {
	return <div className={s.messageWrap}  >
		{props.dataMessage.items.length !== 0
			? props.dataMessage.items.map((item: ItemsMessageType) => {
				let time = item.addedAt.substring(11, 16)
				return <div key={item.id} className={s.oneMessage}>

					<div>{`${item.senderName}  ${time}`}</div>
					<div className={s.bodyT}>
						{item.body}
					</div>

				</div>
			})
			: <div className={s.noMessage}>No messages here yet</div>
		}
	</div>
}

export type ForInitialForm = {
	message: string
}
type PropsTypeDialogItemo = {
	userName: string
	id: number
	photo: PhotosType
	getMessageWithFriends: (userId: number) => void
}
type QsForHistory = {
	id?: number
}
type MessagePropsType = {
	dataMessage: MessageServerType
}