import { dialogApi } from '../api/dialogApi';
import { ForInitialForm } from '../component/Dialog/DialogF';
import { PhotosType } from './../commonType/commonType';
import { ActionsTypes, CommonThunkActionType } from './reduxStore';

export type DialogRespondeData = {
	hasNewMessages: boolean
	id: number
	lastDialogActivityDate: string
	lastUserActivityDate: string
	photos: PhotosType
	userName: string
}

type ActionType = ActionsTypes<typeof action>
export type ItemsMessageType = {
	addedAt: string
	body: string
	id: string
	recipientId: number
	senderId: number
	senderName: number
	translatedBody: null | any
	viewed: boolean
}
export type MessageServerType = {
	items: Array<ItemsMessageType>,
	totalCount: number,
	error: null | number,
}
let initializState = {
	dataDialogs: [] as Array<DialogRespondeData>,
	dataMessage: {
		items: [],
		totalCount: 0,
		error: null,
	} as MessageServerType
}

const dlgReducer = (state = initializState, action: ActionType) => {
	switch (action.type) {
		case 'GETDIALOG':
			return {
				...state, dataDialogs: [...action.arrivedDilogs]
			}
		case 'GET_MESSAGE_FOR_USER':
			return {
				...state, dataMessage: { ...action.arrivedData }
			}
		default:
			return state
	}

}
const action = {
	getMessage: (arrivedData: MessageServerType) => ({ type: 'GET_MESSAGE_FOR_USER', arrivedData } as const),
	getDialogs: (arrivedDilogs: Array<DialogRespondeData>) => ({ type: 'GETDIALOG', arrivedDilogs } as const)
}
type ThunkType = CommonThunkActionType<ActionType>

export const getDialogTh = (): ThunkType =>
	async (dispatch) => {
		let data = await dialogApi.getAllDialogs()
		dispatch(action.getDialogs(data.data))
	}
export const getMessageForUserTh = (userId: number): ThunkType =>
	async (dispatch) => {
		let data = await dialogApi.getMessageWithFrends(userId)
		dispatch(action.getMessage(data.data))
	}
export const sendMessageFriendTh = (userId: number, body: ForInitialForm): ThunkType =>
	async (dispatch) => {

		let res = await dialogApi.sendMessageWithFrends(userId, body)
		if (res.data.resultCode === 0) {
			dispatch(getMessageForUserTh(userId))
		}
	}
export const createDialogTh = (userId: number): ThunkType => async (dispatch) => {
	let data = await dialogApi.createDialogWithFrends(userId)

}


export default dlgReducer
