import { instance } from "./api"

export const dialogApi = {
	createDialogWithFrends(userId: number) {

		return instance.put(`dialogs/${userId}`)
			.then(res => {
				return res
			})
	},
	getAllDialogs() {
		return instance.get(`dialogs`)
			.then(res => {
				return res
			})
	},
	getMessageWithFrends(userId: number) {
		return instance.get(`dialogs/${userId}/messages`)
			.then(res => {
				return res
			})
	},
	sendMessageWithFrends(userId: number, body: any) {
		return instance.post(`dialogs/${userId}/messages`, { body: body.message })
			.then(res => {
				return res
			})
	}
}