import { Button } from 'antd';
import { useDispatch } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { UserDataType } from '../../../commonType/commonType';
import userIcon from '../../../img/img.webp'
import { createDialogTh } from '../../../redux/dlgReduser';
import s from './Friend.module.css';

type PropsType = {
	item: UserDataType
}
const Friend: React.FC<PropsType> = ({ item }) => {
	const dispatch = useDispatch()
	const createDialog = (userId: number) => {
		dispatch(createDialogTh(userId))
	}
	return (
		<div className={s.friend}>
			<div className={s.img}>
				<NavLink to={`/profile/${item.id}`}>
					<img className={s.photo} alt={''} width="40" height="40"
						src={(item.photos.small != null) ? item.photos.small : userIcon} />
					<span className={s.name}>{item.name}</span>
				</NavLink>
				<NavLink to={`/dialogs?id=${item.id}`}>
					<Button onClick={() => createDialog(item.id)} >write message</Button>
				</NavLink>

			</div>

		</div >
	);
}
export default Friend;

